.History {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    font-size: 2.5rem;
    font-weight: 400;
    color: white;
}

.History .title {
    margin: 20px 0 10px;
}

.History .list {
    width: 100%;
    font-size: 1rem;
}

.History .list .hour {
    font-style: italic;
}

.History .list .kCalTotals {
  font-style: italic;
  font-size: 12px;
  opacity: 0.6;
}

.History .itemContainer {
    min-height: 140px;
    width: calc(100% - 20px);
}
.History .itemContainer .foodList {
  margin: 45px 0 !important;
}

.History .categories {
    height: 100%;
    width: 100%;
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    color: #AAA;
}

.History .categories.odd {
    justify-content: flex-end;
}

.History .categories .category {
    margin-right: 10px;
}

.History .prettyPhrase {
    text-align: center;
}
.History .categories .kCal {
  font-style: italic;
  opacity: 0.8;
}